:root {
  --color-white: #fff;
  --primary-blue: #0e6ff9;
  --button-primary-hover: #3e8cfa;
  --button-primary-inactive: #9fc5fd;
  --light-gray: #f2f2f2;
  --dark-gray: #999;
  --color-red: #d9232e;
  --warning-red: #f15354;
  --orange: #f86516;
  --success-green: #20a76c;
  --color-black: #000;
  --primary-text: #29313b;
  --secondary-text: #575a5d;
  --secondary-second: #898b8d;
  --primary-background: #fafafa;
  --interactive-background: #f2f3f5;
  --border: #e0e4e7;
  --button-secondary-pressed: #e7e9eb;
  --font-color: #1d1d1d;
  --shadow-color: rgb(0 0 0 / 10%);
  --card-header-color: #efefef;
  --dm-primary-background: #29313b;
  --dm-secondary-background: #23262a;
  --dm-border: #6e7275;
  --dm-primary-text: #fff;
  --dm-secondary-text: #d6d8db;
  --dm-background: #414851;
  --dm-interactive-background: #414851;
  --dm-shadow-color: lightgray;
  --dm-card-header-color: #494949;
  --dm-editor-background: #282c34;
  --dm-color-editor-autocomplete-selected-background: #004772;
  --navigation-bar-height: 50px;
  --operations-menu-width: 275px;
  --min-page-width: 800px;
  --min-page-height: 600px;
}

.operations-menu {
  --summary-height: 20px;
  --margin-left-spacing: 25px;
  --margin-right-spacing: 20px;
  --plus-button-color: #00a300;
  --plus-button-hover-color: #00cd00;
  --scrollbar-color: rgb(0 0 0 / 40%);
}

.example-gallery {
  --primary-text: #28303b;
  --secondary-text: #555;
  --button-primary-hover: rgb(253 64 64);
  --button-primary-inactive: #c9d4df;
  --dm-button-primary-inactive: #8e9aa6;
  --selected-shadow: rgb(0 0 0 / 25%);
  --dm-selected-shadow: var(--light-gray);
}
