@import '../../variables';

.navigation-bar {
  position: relative;
  box-shadow: 0 0 6px 2px var(--shadow-color);
  overflow: visible;
  z-index: 10;
}

.navigation-bar.dark {
  box-shadow: 0 0 6px 1px var(--dm-shadow-color);
}

.menu {
  height: var(--navigation-bar-height);
  min-height: var(--navigation-bar-height);
  display: flex;
  align-items: center;
  flex-direction: row;
  font-size: 14px;
  font-family: 'Roboto 500', sans-serif;

  .logo {
    margin-left: 5px;

    img {
      height: calc(var(--navigation-bar-height) - 15px);
      margin: 2px 0 0 15px;
    }
  }

  .title {
    flex-grow: 1;
    flex-shrink: 1;
    font-size: 1.25em;
    color: var(--primary-text);
    margin: 2px 10px 0;
    border-left: 1px solid var(--primary-text);
    padding: 5px 10px;
    min-width: 250px;
  }

  .toggle {
    color: var(--primary-text);
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 26px;
    margin-left: 5px;
    margin-right: 15px;

    /* Hide default HTML checkbox */
    & input {
      opacity: 0;
      width: 0;
      height: 0;
    }
  }

  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--primary-text);
    -webkit-transition: 0.4s;
    transition: 0.4s;

    &::before {
      position: absolute;
      content: '🌙';
      height: 22px;
      width: 22px;
      left: 4px;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      padding: 2px;
      font-size: 14px;
      line-height: 22px;
    }

    &:hover {
      background-color: var(--primary-blue);
    }
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 26px;

    &::before {
      border-radius: 50%;
    }
  }

  button {
    margin-right: 20px;
  }

  .button__start-over {
    display: flex;
    align-items: center;
    border-radius: 5px;
    border: 1px solid var(--primary-blue);
    margin-right: 10px;
    cursor: pointer;
    font-size: 14px;
    font-family: 'Roboto 500', sans-serif;
    color: var(--color-white);
    background-color: var(--primary-blue);
    padding: 2px 5px 2px 2px;

    &:hover {
      color: var(--color-white);
      background-color: var(--button-primary-hover);
      border-color: var(--button-primary-hover);
    }

    p {
      margin-left: 2px;
    }
  }

  .icon-label-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    cursor: pointer;
  }

  .examples-icon {
    width: 18px;
    height: 18px;
    margin-right: 8px;
    color: var(--color-black);
    stroke: var(--color-black);
  }

  .your-dossier-icon {
    width: 18px;
    height: 18px;
    margin-right: 8px;
    color: var(--color-black);
  }

  .user-icon {
    width: 14px;
    height: 13px;
    cursor: pointer;
    color: var(--color-black);
    stroke: var(--color-black);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .label-examples {
    cursor: pointer;
  }

  .label-your-dossier {
    cursor: pointer;
    min-width: 80px;
  }

  .user-tooltip {
    color: var(--color-black);

    .button-logout {
      margin-right: 15px;
      cursor: pointer;
      display: flex;
      align-items: center;
      gap: 5px;

      &:hover {
        color: var(--color-red);
      }
    }

    .user-menu-item {
      cursor: pointer;
      font-size: 14px;
      line-height: 18px;
      font-weight: 100;
      margin: 10px;
    }

    .user-name-container {
      display: flex;
      justify-content: left;
      align-items: center;
      margin: 10px;
      font-size: 16px;
    }

    .avatar {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: #8abfd9;
      color: var(--color-white);
      font-size: 14px;
      font-weight: bold;
      margin-right: 10px;
      cursor: default;
    }

    .link-text {
      text-decoration: none;
      color: var(--color-black);
    }
  }
}

.dark.menu {
  background-color: var(--dm-primary-background);

  .toggle,
  .title {
    color: var(--dm-primary-text);
  }

  .title {
    border-left: 1px solid var(--dm-primary-text);
  }

  .slider {
    background-color: var(--color-white);

    &::before {
      content: '☀️';
    }

    &:hover {
      background-color: var(--primary-blue);
    }
  }

  input:checked + .slider::before {
    -webkit-transform: translateX(15px);
    -ms-transform: translateX(15px);
    transform: translateX(15px);
  }

  .icon-label-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    cursor: pointer;
  }

  .examples-icon {
    color: var(--color-white);
    stroke: var(--color-white);
  }

  .your-dossier-icon {
    color: var(--color-white);
  }

  .user-icon {
    color: var(--color-white);
    stroke: var(--color-white);
  }

  .label-examples {
    cursor: pointer;
  }

  .label-your-dossier {
    cursor: pointer;
  }

  .user-tooltip {
    color: var(--dm-secondary-text);

    .button-logout {
      margin-right: 15px;
      cursor: pointer;
      display: flex;
      align-items: center;
      gap: 5px;

      &:hover {
        color: var(--color-red);
      }
    }

    .user-menu-item {
      cursor: pointer;
      font-size: 14px;
      line-height: 18px;
      color: white;
      font-weight: 100;
      margin: 10px;
    }

    .user-name-container {
      display: flex;
      justify-content: left;
      align-items: center;
      margin: 10px;
      font-size: 16px;
    }

    .avatar {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: #8abfd9;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
      margin-right: 10px;
      cursor: default;
    }

    .link-text {
      text-decoration: none;
      color: white;
    }
  }
}
