// tooltip onboarding

.onboarding-tooltip {
  background-color: white;
  display: flex;
  width: 626px;
  height: 450px;
  user-select: none;

  .onboarding-tooltip-title-container {
    display: flex;
    align-items: center;
    padding: 15px;
  }

  .onboarding-tooltip-title-image {
    width: 34px;
    height: 34px;
  }

  .onboarding-tooltip-title-label {
    margin-left: 10px;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: black;
  }

  .onboarding-tooltip-image {
    background-color: #a93230;
    width: 273px;
    display: flex;
    flex-direction: column;
    padding: 24px 24px 13px;
    justify-content: space-between;
  }

  .onboarding-tooltip-image-tour-gif-container {
    display: flex;
  }

  .onboarding-tooltip-image-accessibility-info-container {
    width: 222px;
    height: 129px;
  }

  .accessibility-info-image {
    width: 100%;
    height: 100%;
  }

  .onboarding-tooltip-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
  }

  .onboarding-tooltip-content {
    display: flex;
    flex-grow: 2;
    padding: 0 15px;
    flex-direction: column;
    text-align: left;

    .onboarding-tooltip-content-summary {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: #35383a;
    }

    .onboarding-tooltip-content-separator {
      height: 0;
      border: 0.5px solid #c9d4df;
      margin: 10px 0;
    }

    .onboarding-tooltip-content-detail {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #54575f;

      .onboarding-tooltip-content-detail-item {
        margin-bottom: 10px;
      }
    }
  }

  .onboarding-tooltip-footer {
    display: flex;
    flex-grow: 1;
    flex-direction: row;
    justify-content: right;
    align-items: center;
    margin-right: 20px;

    .btn__next {
      background-color: #d9232e;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 90px;
      height: 45px;
      right: 20px;
      bottom: 35px;
      font-size: 20px;
      font-weight: 600;
      color: white;
      cursor: pointer;

      &:hover {
        filter: brightness(110%);
      }
    }

    .btn__skip {
      display: flex;
      margin-right: 20px;
      justify-content: center;
      align-items: center;
      color: #54575f;
      cursor: pointer;

      &:hover {
        color: #d9232e;
      }
    }
  }

  .progress-red {
    list-style: none;
    margin: 0;
    padding: 0;
    position: relative;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
  }

  .progress-red li {
    display: inline-block;
    margin: 0 2.5px;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    border: 1px solid #d9232e;
  }

  .progress-red li.active {
    background-color: #d9232e;
    height: 10px;
    width: 10px;
    border-radius: 50%;
  }
}

// Modal Onboarding

.onboarding-modal {
  background: radial-gradient(50% 50% at 50% 50%, #c53736 73.96%, #a7302e 100%);
  display: flex;
  width: 755px;
  height: 530px;
  justify-content: center;
  align-items: center;
  text-align: center;
  user-select: none;

  .onboarding-modal-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .onboarding-modal-title {
      flex-grow: 1;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: white;
      min-width: 440px;
      max-height: 30px;
    }

    .onboarding-modal-content {
      flex-grow: 1;
      font-weight: 400;
      font-size: 16px;
      line-height: 16.8px;
      color: white;
      margin: 15px;
      text-align: center;
      min-width: 700px;
    }

    .onboarding-modal-image {
      // background-color: red;
      // width: 540px;
      // height: 285px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 20px;
    }

    .onboarding-modal-footer {
      display: flex;
      flex-grow: 1;
      flex-direction: row;
      justify-content: space-between;
      position: absolute;
      bottom: 57px;

      .btn__skip {
        width: 115px;
        height: 32px;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 6px 10px;
        font-size: 15px;
        line-height: 19px;
        color: white;
        border: 1px solid #f6f8fa;
        cursor: pointer;

        &:hover {
          background-color: #f6f8fa;
          color: #c53736;
          border-color: #c53736;
        }
      }

      .btn__continue {
        width: 115px;
        height: 32px;
        background: white;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 6px 10px;
        font-size: 15px;
        line-height: 19px;
        margin-left: 10px;
        cursor: pointer;
        color: #575a5d;

        &:hover {
          background-color: #f6f8fa;
          color: #c53736;
          border-color: #c53736;
        }
      }
    }

    .progress {
      display: flex;
      position: absolute;
      bottom: 25px;
      cursor: pointer;
    }

    .progress li {
      display: inline-block;
      margin: 0 2.5px;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      border: 1px solid #fff;
    }

    .progress li.active {
      background-color: #fff;
      height: 10px;
      width: 10px;
      border-radius: 50%;
    }
  }
}
