@import '../../variables';

.code-mirror {
  .panel-header {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;

    .title {
      flex: 1;
      font-family: 'Roboto 500', sans-serif;
      margin-left: 10px;
      font-size: 16px;
      color: var(--font-color);
    }

    .buttons {
      display: flex;

      .button__run {
        display: flex;
        align-items: center;
        border-radius: 5px;
        border: 1px solid var(--primary-blue);
        cursor: pointer;
        font-size: 14px;
        font-family: 'Roboto 500', sans-serif;
        color: var(--color-white);
        background-color: var(--primary-blue);
        padding: 2px 5px 2px 0;
        margin: 5px 10px 5px 0;

        &:hover {
          color: var(--color-white);
          background-color: var(--button-primary-hover);
          border-color: var(--button-primary-hover);
        }
      }
    }
  }

  .code-mirror-container {
    border-top: 1px solid var(--border);
    background-color: var(--color-white);
  }

  .code-mirror-container.dragging {
    /* stylelint-disable-next-line selector-class-pattern -- this selector is from CodeMirror */
    .cm-line.cm-activeLine {
      // use box-shadow to avoid changing selection div height
      box-shadow: 0 1px 0 var(--color-red) inset;
    }
  }

  .cm-scroller:focus {
    outline: none;
  }

  .cm-editor.cm-focused {
    outline: none;
  }

  .cm-content,
  .cm-gutter {
    min-height: 450px;
  }
}

// Special dark mode settings.
// Background color is set by global dark style
.code-mirror.dark {
  .title {
    color: var(--dm-primary-text);
  }

  .code-mirror-container {
    border-color: var(--dm-border);
  }

  // auto complete selected
  .cm-tooltip.cm-tooltip-autocomplete > ul > li[aria-selected] {
    background: var(--dm-color-editor-autocomplete-selected-background);
  }
}
