@import './variables';

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url('./fonts/material-design-icons/MaterialIcons-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Material Icons Outlined';
  font-style: normal;
  font-weight: 400;
  src: url('./fonts/material-design-icons/MaterialIconsOutlined-Regular.otf') format('opentype');
}

.material-icons,
.material-icons-outlined {
  font-weight: normal;
  font-style: normal;
  font-size: 20px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;

  /* Support for Safari and Chrome. */
  // The value `optimizeLegibility` does not follow all lowercase format
  /* stylelint-disable-next-line value-keyword-case */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
}

.material-icons {
  font-family: 'Material Icons', sans-serif;
}

.material-icons-outlined {
  font-family: 'Material Icons Outlined', sans-serif;
}

@font-face {
  font-family: Roboto;
  src: url('./Roboto/Roboto-Regular.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}

@font-face {
  font-family: 'Roboto 500';
  src: url('./Roboto/Roboto-Medium.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}
@font-face {
  font-family: 'Roboto Bold';
  src: url('./Roboto/Roboto-Bold.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nodisplay {
  display: none;
}

.app {
  min-width: var(--min-page-width);

  input[type='button'] {
    font-family: Roboto, sans-serif;
  }

  /* stylelint-disable selector-class-pattern -- this selector is from react-tooltip */
  .__react_component_tooltip.place-top::after {
    border-top: 8px solid var(--color-white);
  }

  .__react_component_tooltip.place-bottom::after {
    border-bottom: 8px solid var(--color-white);
  }

  .__react_component_tooltip.place-left::after {
    border-left: 8px solid var(--color-white);
  }

  .__react_component_tooltip.place-right::after {
    border-right: 8px solid var(--color-white);
  }
  /* stylelint-enable */

  .react-tooltip {
    background-color: var(--color-white);
    opacity: 1 !important;
    box-shadow: 0 2px 6px var(--dark-gray);
    padding: 10px;
    cursor: auto;

    // needed to fix button react-tooltip hidden below CodePanel
    z-index: 2;
  }

  .tooltip-text {
    font-size: 14px;
    font-family: 'Roboto 500', sans-serif;
    color: var(--primary-text);
  }

  button {
    border: none;
    background-color: inherit;
    font-family: 'Roboto 500', sans-serif;
    color: var(--secondary-text);
    cursor: pointer;
    font-size: 14px;
    padding: 5px 10px;

    &:hover {
      color: var(--font-color);
    }
  }

  .content {
    min-width: var(--min-page-width);
    background-image: none;
    height: calc(100% - var(--navigation-bar-height));
    display: flex;
    position: absolute;
    width: 100%;
    overflow: hidden;
    background-color: var(--primary-background);

    // Overwrite operations menu resizer 'col-resize' cursor with 'default' cursor.
    /* stylelint-disable-next-line selector-class-pattern -- this selector is from react-collapse-pane */
    .Resizer.vertical.top-level-collapse-pane {
      cursor: default;
    }

    // Used to hide resizer when tooltip on operation item is hovered.
    .hidden-resizer {
      z-index: 0;
      opacity: 0;
    }

    .full-screen-button-container {
      z-index: 5;
      position: absolute;
      display: flex;
      flex-direction: column;
      -webkit-box-align: center;
      align-items: center;
      -webkit-box-pack: center;
      justify-content: center;
      height: 0.5rem;
      right: 0;
      left: 0;
      top: 0;
      bottom: 0;
    }

    .full-screen-button {
      position: absolute;
      width: 1.8rem;
      height: 1.8rem;
      border-radius: 300px;
      background: #0e6ff9;
      cursor: pointer;
      color: white;
      border: 2px rgb(200 200 200 / 10%) solid;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      z-index: 3;
      visibility: hidden;
      opacity: 0;
      transition: all 220ms;
    }

    .full-screen-button-container:hover > .full-screen-button {
      visibility: visible;
      opacity: 1;
      transition: all 250ms;
    }

    /* stylelint-disable selector-class-pattern -- this selector is from react-collapse-pane */
    // Need this for safari to display operation item tooltip properly
    .Pane.vertical.top-level-collapse-pane {
      overflow: visible;
    }

    // add transition to operations menu during expanding.
    .SplitPane.vertical.top-level-collapse-pane
      > .Pane.vertical.top-level-collapse-pane:first-child {
      transition: flex-basis 350ms, min-width 350ms, flex-grow 350ms;
    }
    /* stylelint-enable */

    .operations-menu {
      background-color: var(--color-white);
      font-family: Roboto, sans-serif;
    }

    .non-operations-menu {
      background-color: var(--interactive-background);
      display: flex;
      flex: 1;
      flex-direction: column;
      overflow-y: scroll;
      overflow-x: hidden;

      .code-panel-and-events-panel {
        display: flex;
        background-color: var(--interactive-background);

        .code-panel {
          height: 100%;

          .cm-scroller {
            overflow: auto;
          }
        }

        // Code panel class when events panel has been minimized
        .code-panel-no-events {
          .cm-scroller {
            overflow: auto;
          }
        }

        .events-panel {
          flex: 1;
          display: flex;
          position: relative;
          height: 100%;
        }
      }

      // Code and Events panel class when operations menu has been minimized
      .code-and-events-no-op {
        display: flex;
        background-color: var(--interactive-background);
        max-height: 50vh;

        .code-panel {
          .cm-scroller {
            overflow: auto;
          }
        }

        // Code panel class when events panel has been minimized
        .code-panel-no-events {
          width: 100vw;

          .cm-scroller {
            overflow: auto;
          }
        }

        .events-panel {
          flex: 1;
          display: flex;
          max-width: calc(25vw - 5px);
        }
      }
    }
  }

  .content.dark {
    background-color: var(--dm-primary-background);

    .operations-menu {
      background-color: var(--dm-primary-background);
    }

    .non-operations-menu {
      .code-panel-and-events-panel {
        .code-panel {
          .size-button__max {
            .code-max {
              color: var(--dm-secondary-text);
            }
          }
        }

        .code-panel-no-events {
          .size-button__min {
            .code-min {
              color: var(--dm-secondary-text);
            }
          }
        }
      }

      .code-and-events-no-op {
        .code-panel {
          border-right: 2px solid var(--dm-border);

          .size-button__max {
            .code-max {
              color: var(--dm-secondary-text);
            }
          }
        }

        .code-panel-no-events {
          .size-button__min {
            .code-min {
              color: var(--dm-secondary-text);
            }
          }
        }
      }
    }
  }
}

// The CSS for card style header.
// * Fixed height 40px
// * No bottom margin
// * rounded corner on top
.card-header {
  padding: 5px;
  border-radius: 10px 10px 0 0;
  background-color: var(--card-header-color);
  height: 40px;
}

.card-shadow {
  border-radius: 10px 10px 0 0;
  box-shadow: 0 0 8px 0 rgb(0 0 0 / 38%);
  margin: 15px 10px 10px;
}

// Dark mode for all
.dark.app {
  // Fixing scroll bar color. Ref: https://developer.mozilla.org/en-US/docs/Web/CSS/color-scheme
  color-scheme: dark;
  background-color: var(--dm-primary-background);
  border-color: var(--dm-border);
  color: var(--dm-secondary-text);

  .card-header {
    background-color: var(--dm-card-header-color);
  }

  .card-shadow {
    box-shadow: 0 0 6px lightgray;
  }

  button {
    color: var(--dm-secondary-text);

    &:hover {
      color: var(--dm-primary-text);
    }
  }

  /* stylelint-disable selector-class-pattern -- this selector is from react-tooltip */
  .__react_component_tooltip.place-top::after {
    border-top: 8px solid var(--dm-secondary-background);
  }

  .__react_component_tooltip.place-bottom::after {
    border-bottom: 8px solid var(--dm-secondary-background);
  }

  .__react_component_tooltip.place-left::after {
    border-left: 8px solid var(--dm-secondary-background);
  }

  .__react_component_tooltip.place-right::after {
    border-right: 8px solid var(--dm-secondary-background);
  }
  /* stylelint-enable */

  .react-tooltip {
    background-color: var(--dm-secondary-background);
    box-shadow: 0 2px 6px var(--light-gray);
  }

  .tooltip-text {
    color: var(--dm-primary-text);
  }
}
