@import '../../variables';

.preview-panel {
  display: flex;
  font-family: Roboto, sans-serif;
  min-height: 450px;
  height: calc(100% - 20px);
}

.embedding-iframe {
  border: none;
  overflow-y: hidden;
  width: 100%;
  height: 100%;
  padding: 0;

  // use white background to match dossier even in dark mode
  background-color: var(--color-white);
}

.embedding-iframe.card-shadow {
  // Override margin-top to remove some space
  margin-top: 10px;
}
