@import '../../variables';

/* Due to react-modal, the css of this component may not inherit from the main app css */

.example-gallery {
  position: absolute;
  width: 980px;
  height: 647px;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  align-items: center;
  margin: auto;
  background-color: var(--primary-background);
  border-radius: 10px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  color: inherit;
  z-index: 1000;

  /* z-index need to be higher than rest of app to keep this popup over them */

  &:focus {
    outline: none;
  }

  .link-button {
    background-color: inherit;
    color: var(--color-red);
    border: 2px solid var(--color-red);

    &:hover {
      filter: brightness(120%);
    }

    &--disabled {
      background-color: inherit;
      color: var(--button-primary-inactive);
      border: 2px solid var(--button-primary-inactive);
      cursor: default;
      pointer-events: none;

      &:hover {
        cursor: default;
        pointer-events: none;
      }
    }
  }

  &-container {
    &__title {
      text-align: center;
      padding: 11px 0;
      box-shadow: 0 1px var(--dark-gray);
      font-weight: 600;
      font-size: 14px;

      &__closeButton {
        position: absolute;
        cursor: pointer;
        right: 13px;
        top: 10px;
        color: var(--secondary-text);

        &:hover {
          color: var(--primary-text);
        }
      }
    }

    &__content {
      display: flex;
      flex-direction: row;

      &__left-bar {
        flex: 1 1 20%;
        margin: 32px 24px;
        max-height: 540px;
        overflow-y: auto;
        overflow-x: hidden;
        line-height: 26px;
        size: 18px;

        .your-dossier-group {
          display: flex;
          flex-direction: column;
          border-bottom: 1px solid var(--dark-gray);
          padding-bottom: 10px;
          cursor: pointer;
          color: var(--secondary-text);

          &--selected {
            color: var(--primary-text);
            font-weight: 600;
          }

          &:hover {
            font-weight: 600;
            color: var(--primary-text);
          }
        }

        .example-groups {
          display: flex;
          flex-direction: column;
          padding-top: 12px;
          row-gap: 24px;
          color: var(--secondary-text);

          .example-group {
            cursor: pointer;

            &--selected {
              font-weight: 600;
              cursor: pointer;
              color: var(--primary-text);
            }

            &:hover {
              font-weight: 600;
              color: var(--primary-text);
            }
          }
        }
      }

      &__body {
        flex: 1 1 80%;
        display: flex;
        flex-direction: column;
        row-gap: 25px;

        &__top-desc {
          padding-top: 32px;

          .top-desc {
            display: flex;
            row-gap: 20px;
            flex-direction: column;

            &__title {
              font-weight: 600;
              font-size: 20px;
            }

            &__desc {
              max-width: 98%;
              display: block;
              height: 42px;
            }
          }
        }

        &__examples-container {
          flex-grow: 1;
          width: 100%;
          align-self: flex-start;
          display: flex;
          flex-flow: row wrap;
          row-gap: 24px;
          overflow-y: auto;
          overflow-x: hidden;
          column-gap: 25px;
          max-height: 350px;

          .example-container {
            cursor: pointer;
            min-width: calc(33.3333% - 25px);
            max-width: calc(33.3333% - 25px);
            flex-basis: calc(33.3333% - 25px);
            min-height: calc(50% - 24px);
            max-height: calc(50% - 24px);
            padding: 6px;

            &__img {
              height: 114px;
              width: 100%;
              padding: 6px;
              border-radius: 3px;
            }

            &__name {
              text-align: center;
              padding-top: 4px;
              color: var(--secondary-text);
            }

            &:hover {
              font-weight: 600;
            }

            &--selected {
              .example-container__img {
                box-shadow: 0 0 10px var(--selected-shadow);
              }

              .example-container__name {
                font-weight: 600;
              }
            }
          }
        }

        &__buttons-bar {
          display: flex;
          position: absolute;
          bottom: 20px;
          right: 20px;
          column-gap: 10px;
        }

        .url-input {
          width: calc(100% - 8px);
          height: 50px;
          align-self: center;
          padding: 8px;
          background-color: inherit;
          font-size: 16px;
          border: 2px solid var(--dm-secondary-text);
        }

        .url-input:focus {
          outline: none;
          border-color: var(--dark-gray);
        }

        .error-message {
          height: 47px;
          margin-top: 8px;
          align-items: left;
          text-align: left;
          color: var(--color-red);

          &__closed {
            display: none;
          }

          &__open {
            font-size: 14px;
            display: block;
          }
        }
      }
    }

    &__button {
      height: 40px;
      width: 110px;
      padding: 10px;
      border: none;
      border-radius: 3px;
      background-color: var(--color-red);
      color: var(--color-white);
      font-size: 14px;
      font-weight: bold;
      cursor: pointer;
      pointer-events: auto;

      &:hover {
        filter: brightness(110%);
      }

      &--disabled {
        cursor: default;
        pointer-events: none;
        background-color: var(--button-primary-inactive);
      }
    }
  }
}

.example-gallery.dark {
  background-color: var(--dm-primary-background);

  // Fixing scroll bar color. Ref: https://developer.mozilla.org/en-US/docs/Web/CSS/color-scheme
  color-scheme: dark;

  .link-button {
    &--disabled {
      color: var(--dm-button-primary-inactive);
      border-color: var(--dm-button-primary-inactive);
    }
  }

  .example-gallery-container {
    &__title {
      color: var(--dm-primary-text);

      &__closeButton {
        color: var(--dm-secondary-text);

        &:hover {
          color: var(--dm-primary-text);
        }
      }
    }

    &__content {
      color: var(--dm-primary-text);

      .example-groups {
        color: var(--dm-secondary-text);

        .example-group {
          &--selected {
            color: var(--dm-primary-text);
          }

          &:hover {
            color: var(--dm-primary-text);
          }
        }
      }

      .your-dossier-group {
        color: var(--dm-secondary-text);

        &--selected {
          color: var(--dm-primary-text);
        }

        &:hover {
          color: var(--dm-primary-text);
        }
      }

      .example-container {
        &__name {
          color: var(--dm-secondary-text);
        }
      }

      .example-container--selected {
        .example-container__img {
          box-shadow: 0 0 10px var(--dm-selected-shadow);
        }

        .example-container__name {
          color: var(--dm-primary-text);
        }
      }
    }

    .placeholder {
      color: var(--dm-secondary-text);
    }

    .url-input {
      color: var(--dm-primary-text);
      border-color: var(--dm-secondary-text);
    }

    &__button {
      &--disabled {
        background-color: var(--dm-button-primary-inactive);
      }
    }
  }
}

.example-gallery-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(#000, 0.6);
  z-index: 1000;

  /* z-index need to be higher than rest of app to keep overlay over them */
}
