@import '../../variables';

.operations-menu {
  max-height: calc(100vh - 10px);
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  border-right: 2px solid var(--border);
  margin-right: 5px;
  margin-left: 0;

  a {
    text-decoration: none;
  }

  // Menu title
  .head {
    font-weight: 500;
    font-weight: bold;
    font-size: 18px;
    font-family: Roboto-Medium, sans-serif;
    position: relative;
    text-align: left;
    align-items: center;
    margin-left: var(--margin-left-spacing);
    margin-top: 20px;
    margin-bottom: 20px;
    flex-grow: 0;
  }

  /* Search Bar */
  .search-bar {
    height: 32px;
    padding: 5px;
    position: relative;
    margin-left: var(--margin-left-spacing);
    margin-right: var(--margin-right-spacing);
    margin-bottom: 15px;
    z-index: -1;
    border: none;

    input:focus {
      border: none;
      outline: none;
    }

    .search-input::placeholder {
      color: var(--secondary-text);
    }

    .search-input {
      width: 100%;
      height: 32px;
      position: absolute;
      background-color: var(--light-gray);
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 5px;
      border: none;
      color: var(--primary-text);
      font-size: 14px;
      padding: 5px 5px 5px 28px;
    }
  }

  .details-wrapper {
    height: 100%;
    flex: 1;
    overflow-y: auto;
    user-select: none; // For latest versions of Chrome, Firefox, Edge
    -webkit-user-select: none; // For Safari
    -ms-user-select: none; // For IE and older edge versions
  }

  .details-wrapper::-webkit-scrollbar {
    width: 7px;
  }

  .details-wrapper::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-color);
    border-radius: 5px;
  }

  // Contains all drop downs and summaries
  .operation-item-list {
    text-align: left;
    display: flex;
    position: relative;
    flex-direction: column;
    background: transparent;
    margin-bottom: 0;
    width: 100%;

    // // We set it to no display to prevent draggable bug
    // .__react_component_tooltip {
    //     display: none;
    // }
    .operation-item {
      align-items: center;
      cursor: grab;
      list-style: none;
      list-style-position: inside;
      text-align: left;
      font-size: 15px;
      position: relative;
      display: flex;
      font-weight: 400;
      line-height: 1.5;
      vertical-align: middle;
      padding: 5px var(--margin-right-spacing) 5px var(--margin-left-spacing);
      background-color: transparent;

      &:hover {
        background-color: var(--interactive-background);
      }

      // Element within each list
      // Conntains drag and drop, and tooltip
      &:nth-child(1) {
        margin-top: 5px;
      }

      // Drag and drop operation item
      .item {
        display: inline-flex;
        width: 100%;
        padding-left: 12px;
        padding-right: 9px;
        padding-top: 3px;

        .item__text {
          color: var(--primary-text);
          font-size: 14px;
          line-height: 16px;
          font-family: 'Roboto 500', sans-serif;
        }
      }

      // Info sign for tool tip hidden
      .info {
        display: flex;
        cursor: grab;
        visibility: hidden;
        flex-direction: row;
        align-items: center;
        justify-content: left;
        position: relative;

        .information-fill {
          cursor: pointer;
          text-decoration: none;
          font-style: normal;
          color: var(--font-color);

          &:hover {
            color: var(--primary-blue);
          }

          &::before {
            font-family: 'Material Icons Outlined', sans-serif;
            content: 'info';
            font-size: 20px;
          }
        }
      }

      &:hover > .info {
        visibility: visible;

        .information-fill {
          text-decoration: none;
          font-style: normal;
          color: var(--font-color);

          &:hover {
            color: var(--primary-blue);
          }

          &::before {
            font-family: 'Material Icons Outlined', sans-serif;
            content: 'info';
            font-size: 20px;
          }
        }
      }

      // // We set it to display the tooltip when it is not possible to drag
      // .information-fill:hover + .__react_component_tooltip {
      //     display: block;
      // }

      .tooltip {
        display: flex;
        flex-direction: column;
      }

      .tooltip-footer {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        margin-top: 10px;
        font-family: 'Roboto 500', sans-serif;
        vertical-align: middle;
        column-gap: 10px;

        .tooltip-link {
          background-color: inherit;

          a {
            color: var(--primary-blue);
          }

          a:hover {
            color: var(--button-primary-hover);
          }
        }

        .add-code {
          display: flex;
          flex-direction: row;
          float: right;
          text-align: right;
          column-gap: 2px;
          font-family: 'Roboto 500', sans-serif;
          vertical-align: middle;
          color: var(--plus-button-color);

          .add-code__text {
            cursor: pointer;
          }

          &:hover {
            color: var(--plus-button-hover-color);
          }

          .plus-button {
            cursor: pointer;
            font-size: 20px;
          }
        }
      }

      .tooltip-text {
        display: table;
        table-layout: fixed;
        max-width: 200px;
        text-align: left;
        vertical-align: center;
        background-color: var(--color-white);
        color: var(--color-black);
        font-family: Roboto, sans-serif;
      }
    }

    .operation-item:active {
      cursor: grabbing;
      background-color: transparent;

      .info {
        visibility: hidden;
      }
    }
  }

  // @keyframes details-show {
  //   from {
  //     margin-bottom: -20%;
  //     opacity: 0;
  //     transform: translateY(-100%);
  //   }
  // }

  // By default summary will have a marker to the left of it for supported browsers (Safari is one).
  // We want to remove this marker.
  summary::-webkit-details-marker {
    display: none;
  }

  // Drop down menu heading
  summary {
    height: var(--summary-height);
    font-family: Roboto-Medium, sans-serif;
    outline: none;
    display: block;
    font-weight: bold;
    background-color: var(--color-white);
    color: var(--color-black);
    padding-left: var(--margin-left-spacing);
    padding-right: var(--margin-right-spacing);
    position: relative;
    cursor: pointer;
    font-size: 17px;
    z-index: 0;

    &:hover {
      color: var(--color-red);
      z-index: 1;
    }

    &::after {
      font-family: 'Material Icons', sans-serif;
      font-size: 20px;
      content: '\e5e1';
      transform: rotate(90deg);
      float: right;
      z-index: -1;
      transform-origin: center;
      transition: 0.3s transform ease;
      background-color: inherit;
    }
  }

  // Entire dropdown menu; contains summary and operations
  details {
    box-sizing: border-box;
    text-align: left;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    overflow: hidden;
    margin-bottom: 20px;
    transition: all 0.3s linear;

    &:hover::before {
      color: var(--color-red);
    }

    &[open] > summary::after {
      transform: rotate(270deg);
      transition: 0.3s transform ease;
    }

    // animation causes react-tooltip position incorrect, will disable until the issue is fixed.
    // &[open] > .operation-item-list {
    //   animation: details-show 300ms ease-in-out;
    // }
  }

  // Documentation link
  .link {
    border-top: 2px solid var(--border);
    list-style: none;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    font-size: 14px;
    flex: 0;

    .link-text {
      color: var(--primary-blue);
      cursor: pointer;

      &:hover {
        color: var(--color-red);
      }
    }
  }
}

.dark.operations-menu {
  border-color: var(--dm-border);

  .search-bar {
    .search-input {
      color: var(--dm-secondary-text);
      background-color: var(--dm-secondary-background);
    }

    .search-icon::before {
      color: var(--dm-secondary-text);
    }

    input:focus {
      border: none;
    }
  }

  li {
    background-color: inherit;
    color: var(--dm-primary-text);
  }

  body {
    background-color: var(--dm-primary-background);
  }

  details {
    background-color: inherit;
  }

  summary {
    background-color: inherit;
    color: var(--dm-secondary-text);

    &:hover {
      color: var(--color-red);
    }
  }

  /* stylelint-disable-next-line no-descending-specificity -- not sure how to resolve this elegantly */
  .operation-item-list {
    .operation-item {
      &:hover {
        background-color: var(--dm-interactive-background);
      }

      .item {
        padding-top: 3px;

        .item__text {
          color: var(--dm-secondary-text);
          line-height: 16px;
          font-size: 14px;
          font-family: 'Roboto 500', sans-serif;
        }
      }

      .info {
        .react-tooltip {
          .tooltip-text {
            background-color: inherit;
            color: var(--dm-primary-text);
          }

          .tooltip-link {
            background-color: inherit;
          }
        }

        /* stylelint-disable-next-line no-descending-specificity -- not sure how to resolve this elegantly */
        .information-fill {
          color: var(--dm-primary-text);

          &:hover {
            color: var(--primary-blue);
          }
        }
      }
    }

    .operation-item:active {
      cursor: grabbing;
      background-color: transparent;

      .info {
        visibility: hidden;
      }
    }
  }

  .link {
    border-top: 2px solid var(--dm-border);
  }
}
