@import '../../variables';

.events-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 142px;

  .event-container__header {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    flex-shrink: 0;
    min-width: 250px;

    .tabs {
      display: flex;
      flex: 1;
      flex-direction: row;
      column-gap: 32px;
      margin-right: 20px;
      margin-left: 10px;

      .tab {
        font-family: 'Roboto 500', sans-serif;
        font-size: 16px;
        color: var(--secondary-text);
        cursor: pointer;

        &:hover {
          color: var(--font-color);
        }
      }

      .tab-selected {
        font-family: 'Roboto 500', sans-serif;
        font-size: 16px;
        color: var(--font-color);
        cursor: pointer;
        text-underline-offset: 10px;

        // text-decoration: 3px underline var(--primary-blue); // doesn't work for safari.
        text-decoration-line: underline;
        text-decoration-thickness: 3px;
        text-decoration-color: var(--primary-blue);
      }

      .red-circle {
        position: absolute;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: var(--color-red);
        top: 10px;
      }

      .hide {
        display: none;
      }
    }

    .clear-events {
      flex: 0;
      position: absolute;
      right: 0;
      font-family: 'Roboto 500', sans-serif;
      font-size: 14px;
      border: none;
      cursor: pointer;
      background-color: inherit;
      padding: 5px 10px;
      color: var(--secondary-text);

      &:hover {
        color: var(--font-color);
      }
    }

    .copy-events {
      flex: 0;
      position: absolute;
      right: 35px;
      font-family: 'Roboto 500', sans-serif;
      font-size: 14px;
      border: none;
      cursor: pointer;
      background-color: inherit;
      padding: 5px 10px;
      color: var(--secondary-text);

      &:hover {
        color: var(--font-color);
      }
    }
  }

  .event-container__text {
    flex: 1;
    border-top: 1px solid var(--border);
    font-size: 12px;
    font-family: Roboto, sans-serif;
    overflow: auto;
    background-color: var(--color-white);
    font-weight: 400;

    pre {
      margin: 10px;
    }
  }
}

.events-container.dark {
  // Use the same background color as CM
  // background-color: var(--dm-editor-background);
  .event-container__header {
    .tab-selected {
      color: var(--dm-primary-text);
    }

    .tabs {
      .tab {
        color: var(--dm-secondary-text);

        &:hover {
          color: var(--dm-primary-text);
        }
      }
    }

    .clear-events {
      color: var(--dm-secondary-text);

      &:hover {
        color: var(--dm-primary-text);
      }
    }

    .copy-events {
      color: var(--dm-secondary-text);

      &:hover {
        color: var(--dm-primary-text);
      }
    }

    .red-circle {
      top: 10.5px;
    }
  }

  .event-container__text {
    color: var(--dm-primary-text);
    background-color: var(--dm-primary-background);
    border-color: var(--dm-border);
  }
}
